.svgWrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: #edf2f7;
  padding: 20px 50px;
  margin: 20px;
}
.SVGImagesWrapper {
  width: 65%;
}
.VirtualRamWrapper {
  height: 100%;
}
@media (max-width: 900px) {
  .svgWrapper {
    height: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px 0px;
    margin: 0px;
  }
  .SVGImagesWrapper {
    width: 100%;
  }
}
